html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Prompt", sans-serif;
  font-size: 1.6rem;
  background: #16015a;
  background: linear-gradient(201deg, #16015a 0%, #4970f0 80%);
  background-color: -webkit-linear-gradient(201deg, #16015a 0%, #4970f0 80%);
  background-color: -moz-linear-gradient(201deg, #16015a 0%, #4970f0 80%);
  background-repeat: no-repeat;
}

p {
  font-size: 2rem;
  color: #000;
  letter-spacing: 0.1rem;
}

.contenedor {
  width: 100%;
  max-width: 140rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 2rem 0 5rem 0;
}

h1 {
  font-size: 4rem;
  color: white;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 6rem;
  color: #FFF;
}

span {
  font-weight: 700;
}

p {
  text-align: justify;
}

.titulo {
  text-align: center;
  color: green;
}

.btn-enviar {
  padding: 1rem 6rem;
  margin-top: 2rem;
  background-color: rgb(189, 187, 90);
  border: none;
}
.btn-enviar:hover {
  background-color: rgb(42, 243, 243);
}

.btn-registrar {
  padding: 1rem 6rem;
  margin-top: 2rem;
  background-color: rgb(134, 132, 3);
  border: none;
}
.btn-registrar:hover {
  background-color: rgb(12, 9, 243);
}
.btn-registrar:hover a {
  color: #FFF;
}

.header {
  position: relative;
  height: 100%;
  text-align: center;
  align-items: center;
  margin-top: 0;
  /* > esto involucra a los elementos directos */
  /* Submenú */
  /* Acerca de ...*/
  /* contacto */
}
.header h1 {
  margin: 2rem 0;
  padding: 0;
}
@media (min-width: 992px) {
  .header h1 {
    padding: 0;
  }
}
.header .contenido-header {
  max-width: 80%;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    margin-bottom: 0;
  }
}
@media (min-width: 992px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
}
.header nav {
  /*esto nos permite agregarle al menú una posición relativa, esto no sirve para darle a los elementpos una posicion absoluta*/
}
.header nav ul {
  list-style: none;
  padding: 0;
  margin-bottom: 0;
  overflow: hidden;
  display: flex;
  justify-content: center;
}
.header nav > ul > li {
  float: left;
}
.header nav ul li a {
  text-decoration: none;
  color: #ffffff;
  padding: 0 1rem;
}
.header nav ul li:hover {
  background-color: rgb(137, 186, 241);
}
.header .header-servicios {
  position: absolute;
  text-align: center;
  margin: 0;
}
.header nav > ul > li:hover div {
  display: table;
  max-width: 100%;
  margin-top: 0;
  z-index: 3;
}
.header nav ul li div {
  width: 100%;
  max-width: 50rem;
  position: absolute;
  left: 0;
  background-color: #ccc;
  box-sizing: border-box;
  display: none;
}
.header nav ul li div ul {
  width: 15%;
  display: table-cell;
  border-right: 1.5px solid rgba(255, 255, 255, 0.6);
  box-sizing: border-box;
}
.header nav ul li div ul:last-child {
  border: none;
}
.header nav ul li div ul .titulo {
  background-color: #011c68;
  color: #ffffff;
  margin-top: 0.2rem;
}
.header nav ul li div ul .titulo a {
  color: #ffffff;
  padding: 0.2rem 0.2rem;
}
.header .titulo .azul {
  background-color: #3498db;
}
.header .titulo .verde {
  background-color: #27ae60;
}
.header .titulo .naranja {
  background-color: #e67e22;
}
.header .titulo .rojo {
  background-color: #c0392b;
}
.header nav ul li div ul {
  background: none;
}
.header nav ul li div ul a:hover {
  color: black;
  font-size: 1.5rem;
  z-index: 4;
}
.header nav ul li div ul li.titulo a:hover {
  background: none;
  color: white;
}
.header nav ul li div ul li a {
  color: blue;
}
.header .header-servicios ul li {
  position: relative;
  padding: 0.1rem;
  z-index: 4;
}
.header .header-producto ul li {
  position: relative;
  padding: 0.3rem;
  z-index: 4;
}
.header .acerca ul li {
  position: relative;
  padding: 0.3rem;
  text-align: center;
  z-index: 4;
}
.header .contacto ul li {
  position: relative;
  padding: 0.3rem;
  text-align: center;
  z-index: 4;
}
.header a {
  display: block;
  margin: 1rem 0;
  color: #FFF;
  font-size: 1.2rem;
}
.header a:hover {
  color: rgb(167, 207, 21);
}

.principal {
  position: relative;
}
.principal .imagen-principal {
  display: flex;
  margin: 0 auto;
  overflow: hidden;
  justify-content: center;
}
.principal .imagen-principal img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}
.principal .overlay {
  position: absolute;
  height: auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.principal .contenedor-principal {
  z-index: 2;
  letter-spacing: 0.2rem;
  text-align: center;
}
.principal .contenedor-principal h3 {
  color: #f4f3f8;
  font-size: 3rem;
  font-weight: 400;
  padding: 2rem;
  margin: 0;
}
@media (min-width: 768px) {
  .principal .contenedor-principal h3 {
    font-size: 4rem;
    font-weight: 500;
  }
}
.principal .contenedor-principal p {
  color: rgb(228, 227, 238);
  font-size: 2rem;
  font-weight: 400;
  text-align: center;
  margin-top: 0;
}

.zona-trabajos {
  margin-top: 3rem;
  max-width: 80%;
  display: block;
  margin: auto;
  margin-bottom: 3rem;
}
@media (min-width: 992px) {
  .zona-trabajos .grid-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.zona-trabajos .icono img {
  display: block;
  margin: 3rem auto;
  width: auto;
}
.zona-trabajos .texto {
  display: block;
  padding: 0 2rem;
  margin: 0;
  text-align: justify;
}
.zona-trabajos h3 {
  text-align: center;
  margin: 0;
}
.zona-trabajos p {
  font-size: 1.4rem;
  line-height: 2rem;
}
.zona-trabajos .icono:hover {
  box-shadow: 0px 8px 16px 8px rgba(255, 255, 255, 0.5);
  background-color: rgba(65, 60, 155, 0.3);
  transition: 0.5s;
}
.zona-trabajos .icono:hover p {
  color: rgb(218, 224, 228);
}

.contenedor-ofertas {
  width: 80%;
  margin-bottom: 10rem;
  display: grid;
}
@media (min-width: 768px) {
  .contenedor-ofertas {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 992px) {
  .contenedor-ofertas {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
.contenedor-ofertas .producto-oferta {
  background-color: rgb(101, 104, 235);
  margin: 1rem;
  gap: 2rem;
  min-width: 22rem;
  border-radius: 3rem;
}
.contenedor-ofertas .producto-oferta:hover {
  box-shadow: 0 5px 30px 10px #415794;
  transition: 0.5s;
  color: #FFF;
}
.contenedor-ofertas .imagen-producto {
  position: relative;
  height: 40rem;
  border-radius: 3rem;
  align-items: center;
}
.contenedor-ofertas .imagen-producto img {
  position: absolute;
  height: 100%;
  width: 100%;
  position: absolute;
  display: flex;
  background-color: #000;
  border-radius: 3rem;
  margin: auto;
  padding: 0;
}
.contenedor-ofertas .imagen-producto img:hover {
  transform: scale(1.5);
  transition: 0.5s;
}
.contenedor-ofertas .imagen-producto:hover .imagen-grande {
  display: block;
}
.contenedor-ofertas .nombre-oferta {
  text-align: center;
  margin: 2rem 0;
  padding: 0;
}
.contenedor-ofertas .viscosidad {
  font-size: 4rem;
  text-align: center;
  margin: 0;
  padding: 0;
}
.contenedor-ofertas .tipo-producto {
  text-align: center;
  font-size: 2rem;
}
.contenedor-ofertas .descripcion-producto {
  font-size: 2rem;
  text-align: justify;
  padding: 0 2rem;
  color: #FFF;
}
.contenedor-ofertas .precio-oferta {
  font-size: 4rem;
  text-align: center;
  color: #FFF;
  font-weight: 700;
  letter-spacing: 0.2rem;
}

.docum_pdf embed {
  display: block;
  margin: auto;
}

.titulo {
  text-align: center;
  color: rgb(186, 245, 186);
  margin: 2rem 0 0 0;
}

.mapa img {
  display: block;
  margin: auto;
  max-width: 100%;
  height: auto;
}

.principal_filtros {
  position: relative;
  margin-top: 5rem;
}

.contenedor-principal-filtros .contenedor-texto {
  display: block;
  max-width: 80%;
  max-height: 100%;
  margin: auto;
}
.contenedor-principal-filtros .contenedor-texto h3,
.contenedor-principal-filtros .contenedor-texto p {
  color: burlywood;
}
.contenedor-principal-filtros .contenedor-texto h3 {
  margin-top: 3rem;
}
.contenedor-principal-filtros .contenedor-texto p {
  font-size: 1.2rem;
  color: #FFF;
  line-height: 2rem;
  margin: 1rem 0;
}
.contenedor-principal-filtros .contenedor-texto p span {
  font-size: 1.8rem;
}
.contenedor-principal-filtros .titulo-foto-filtro p {
  text-align: center;
  margin-top: 4rem;
}
.contenedor-principal-filtros .imagen-principal-filtros {
  position: relative;
  margin-top: 4rem;
}
.contenedor-principal-filtros .imagen-principal-filtros img {
  width: 100%;
  opacity: 0.1;
}
.contenedor-principal-filtros .foto-con-texto {
  margin-top: 3rem;
}
.contenedor-principal-filtros .foto-con-texto .foto-filtro img {
  display: block;
  width: 100%;
  max-width: 40rem;
  height: auto;
  margin: auto;
}
@media (min-width: 768px) {
  .contenedor-principal-filtros .foto-con-texto {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}

.footer {
  margin: 0 0;
}

.contenedor-productos {
  display: block;
  width: 80%;
  max-width: 140rem;
  justify-content: center;
  padding: 2rem;
}
.contenedor-productos p {
  padding: 0 2rem;
  font-size: 1.4rem;
}
.contenedor-productos h3 {
  text-align: center;
  margin: 2rem 0;
}
.contenedor-productos .precio-producto p {
  text-align: center;
  font-size: 2.3rem;
  font-weight: 900;
  font-family: Arial, Helvetica, sans-serif;
}
.contenedor-productos .producto {
  min-width: 22rem;
