}

.texto {
  width: 80%;
  max-width: 120rem;
  display: block;
  text-align: center;
  margin: 2rem auto 2rem auto;
  padding: 2rem;
}
.texto h3 {
  font-size: 4rem;
  color: #FFF;
}
.texto p {
  text-align: left;
  font-size: 2rem;
  color: #FFF;
  font-weight: 500;
}

.ubicacion img {
  position: relative;
  display: block;
  width: 100%;
  max-width: 50rem;
  height: auto;
  margin: 5rem auto 5rem auto;
  padding: 2rem;
}

.imagen-principal-registro {
  position: relative;
  width: 100%;
  height: 80vh;
  letter-spacing: 0.2rem;
  text-align: center;
  margin-bottom: 30rem;
}
