  opacity: 0.2;
  width: 100%;
}
@media (min-width: 768px) {
  .imagen-principal-registro {
    margin-bottom: 5rem;
  }
}

.formulario {
  position: relative;
  display: block;
  width: 100%;
  max-width: 80vh;
  overflow: hidden;
  padding: 0.5rem;
  margin-left: auto;
  margin-right: auto;
  z-index: 3;
}

fieldset {
  position: relative;
  width: 100%;
  max-width: 50rem;
  background-color: rgba(233, 226, 226, 0.4);
  display: flex;
  margin-left: auto;
  margin-right: auto;
}

.table {
  position: relative;
  width: 100%;
  max-width: 80rem;
  margin-left: auto;
  margin-right: auto;
}

tr {
  position: relative;
  width: 100%;
  max-width: 50rem;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 768px) {
  tr {
    display: flex;
  }
}

td {
  display: block;
  justify-content: center;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

label {
  display: block;
  width: 100%;
  font-size: 1.3rem;
  text-align: left;
  align-items: center;
  padding-left: 1rem;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 768px) {
  label {
    text-align: right;
    padding-right: 1rem;
  }
}

input {
  display: block;
  padding: 0.5rem;
  border-radius: 0.5rem;
  border: none;
  width: 100%;
  max-width: 100rem;
  text-transform: uppercase;
